<template>
  <div>
    <router-link 
      :to="{name : routeToGo}" 
      class="button" 
      v-on="$listeners"  
      v-bind="$attrs"
      :style="{ padding: padding + 'px' }"
    
    >
        <slot/>
    </router-link>
  </div>
</template>

<script>
export default {
    $inheritAttrs: false,
    props: {
        buttonClass: String,
        routeToGo: String,
        padding: {
          default: "15",
          type: String
        }
    }
}
</script>

<style>

  .button {
    width: 60%;
    margin-top: 20px;
    border-radius: 25px;
    background-color: var(--main-bg-color);
    color: var(--subSecondary-bg-color);
    cursor: pointer;
    text-decoration: none;
    border: var(--subMain-bg-color) 1px solid;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    /* font-weight: 600; */
    /* font-size: 1rem; */
    transition: all 0.2s linear;
  }
  .button:hover {
    color: var(--secondary-bg-color);
    background-color: var(--main-bg-color);
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
    box-shadow: 0 7px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

    
  @media screen and (max-width: 900px){
    .button{
      width: 80%;
      font-weight: 200;
      /* padding: 10px; */
    }
  }

  @media screen and (max-width: 550px){
    .button{
      padding: 10px;
    }
  }

</style>