<template>
  <div class="flex-column">
    <div class="logo">
        <span @click="goHome" >&lt;?C.S?&gt;</span>
        <div class="bars" v-on:click="navActive = !navActive">
            <v-icon scale="2" name="bars"/>
        </div>
    </div>
   
    <nav id="icons" class="icons" v-bind:class="{ show_icons: navActive }" v-on:click="toggleNav">
        <router-link :to="{ name: 'Home'}" class="options" v-bind:class="{ selected: home }">
            <v-icon scale="1.5" name="home"/>
            <span>Home</span>
        </router-link>

        <router-link class="options" :to="{ name: 'About'}" v-bind:class="{ selected: about }">
            <v-icon scale="1.5" name="regular/user"/>
            <span>About</span>
        </router-link>

        <router-link class="options" :to="{ name: 'Skills'}" v-bind:class="{ selected: skills }">
            <v-icon scale="1.5" name="code"/>
            <span>Skills</span>
        </router-link>

        <router-link class="options" :to="{ name: 'Projects'}" v-bind:class="{ selected: projects }">
            <v-icon scale="1.5" name="laptop-code"/>
            <span>Projects</span>
        </router-link>

        <router-link class="options" :to="{ name: 'Contact'}" v-bind:class="{ selected: contact }">
            <v-icon scale="1.5" name="regular/envelope"/>
            <span>Contact</span>
        </router-link>
    </nav>

  </div>
</template>



<script>
export default {
    data() {
        return {
            navActive: false,
            home: true,
            about: false,
            skills: false,
            projects: false,
            contact: false,
        }
    },
    props: {
        option: String
    },
    watch: { 
        option: function(newVal){// watch it
            this.ChangeSelectedOption(newVal)
        }
    },
    methods: {
        goHome() {
            this.$router.push({name: 'Home'})
        },
        handleScroll() {
            document.getElementById('icons').classList.toggle("show_icons_on_top", window.scrollY > 1 );  
        },
        toggleNav() {
            this.navActive = !this.navActive
        },
        ChangeSelectedOption(option) {
            switch (option) {
                case '/':
                    this.home = true
                    this.about = false
                    this.skills = false
                    this.projects = false
                    this.contact = false
                    break;
                case '/about':
                    this.home = false
                    this.about = true
                    this.skills = false
                    this.projects = false
                    this.contact = false
                    break;
                case '/skills':
                    this.home = false
                    this.about = false
                    this.skills = true
                    this.projects = false
                    this.contact = false
                    break;
                case '/projects':
                    this.home = false
                    this.about = false
                    this.skills = false
                    this.projects = true
                    this.contact = false
                    break;
                case '/contact':
                    this.home = false
                    this.about = false
                    this.skills = false
                    this.projects = false
                    this.contact = true
                    break;
                default:
                    break;
            }
        }  

    },
    created: function () {
        window.addEventListener('scroll', this.handleScroll);
        window.addEventListener('option', this.ChangeSelectedOption);
    },
    destroyed: function () {
        window.removeEventListener('scroll', this.handleScroll);
    }
}
</script>

<style scoped>


.selected {
    background:  var(--background-bg-color) !important;
    color: var(--subMain-bg-color) !important;
    border: solid 1px var(--subMain-bg-color);
    border-radius: 25%;
}

.show_icons{
    top:40px !important;
    left:0px !important;
    opacity: 1 !important;
}
.show_icons_on_top{
    top:0px !important;
}

h1{
    color: var(--subMain-bg-color);
    margin: 0px;
}

img {
    width: 100%;
    height: 60px;
}


.options{
    cursor: pointer;
    color: var(--subSecondary-bg-color); 
    text-decoration: none;
    /* border-radius: 0%; */
    /* background-color: var(--background-bg-color); */
    /* border: 1px ridge var(--subMain-bg-color); */
}

.options:hover{
    color: var(--subMain-bg-color); 
    /* text-decoration: underline; */
    border: solid 1px var(--subMain-bg-color);
    /* box-shadow: 1px 1px 1px 1px var(--subMain-bg-color); */
    border-radius: 25%;
    /* text-shadow: 1px 5px 5px 5px var(--subMain-bg-color);  */
    transition: .5s;
}

.options > span{
    font-size: .8em;
}

.bars {
    display: none;
}

.flex-column{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.logo{
    padding-top: 10px;
} 

.logo span{
    cursor: pointer;
    padding: 2px;
    font-size: 1.2rem;
    border: 1px inset var(--subMain-bg-color);
    box-shadow: 3px 3px var(--subMain-bg-color);
    background-color: var(--background-bg-color);
    color: var(--secondary-bg-color);
}

.icons {    
    display: flex;
    flex-direction: column;
    text-align: center;
    /* background-color: black; */
    height: 90vh;
    /* margin-top: auto; */
    /* margin-top: 3em; */
    align-items: center;
    justify-content: center;
}

.options {
    margin: 20px 0px 20px 0px
}
.options svg,span {
    width: 100%;
}


@media screen and (max-width: 900px){
    .flex-column{
        display: flex;
        flex-direction: row;
        height: 100%;
        justify-content: center;
        align-items: center;
        margin-top: .4em;
    }

    .logo{
        height: 100%;
        /* display: flex; */
        /* justify-content: center; */
    } 

    .icons {
        display: flex;
        flex-direction: row;
        margin-left: auto;
        height: 100%;
        margin-top: 0px;
    }

    .options{
        margin: 0px 5px;
        padding: 5px;
        display: inline;
    }

    .icons > svg {
    }
}

@media screen and (max-width: 550px){
    .flex-column{
        display: flex;
        flex-direction: row;
        /* position: fixed; */
        /* height: 100%; */
        /* justify-content: center;
        align-items: center; */
        /* background: aqua; */
        flex-wrap: wrap;
    }

    .bars {
        margin-left: auto;
