        align-self: flex-end;
        /* background: darkgoldenrod; */
        
    }

    .logo{
        width: 100%;
        padding-top: 0px;
        align-self: flex-start;
        display: flex;
    } 

    .logo > span {
        align-self: flex-start;
        width: 20%;
    }

    .icons {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        /* flex-wrap: wrap; */
        position: fixed;
        z-index: 1;
        top: 40px;
        left: 100%;
        /* height: 10%; */
        width: 100%;
        opacity: 1;
        background-color: var(--main-bg-color);
        /* background: rgb(5, 20, 54, .6); */
        transition: all .7s ease-in-out;
    }

    .options {
      color: var(--secondary-bg-color);
